export default {
    props: {
        heading: {
            type: String,
            default: 'Heading 1'
        },
        expanded: {
            type: Boolean,
            default: false
        },
        light: {
            type: Boolean, 
            default: false
        },
        noMargin: {
            type: Boolean,
            default: false
        },
        contentOverflow: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            active: false
        }
    },
    watch: {
        expanded(newVal) {
            if(newVal) this.active = true;
        }
    },
    mounted() {
        if(this.expanded && this.$refs.content !== undefined) {
            this.active = true;
        }
        if(this.heading === "Base Color") {
            this.toggle();
            setTimeout(() => this.toggle(), 600);
        }
    },
    computed: {
        maxHeight() {
            return this.active ? this.$refs.content.scrollHeight + 'px' : '0px';
        }
    },
    methods: {
        toggle() {
            this.active = !this.active;
            // this.maxHeight = this.active ? this.$refs.content.scrollHeight + 'px' : '0px';
        },
        open() {
            this.active = true;
            this.updateHeight();
        },
        updateHeight() {
           // this.maxHeight = this.$refs.content.scrollHeight + 'px';
        }
    }
}
